import React from "react";
import PropTypes from "prop-types";
/* Emerald */
import Avatar from "emerald-ui/lib/Avatar";
/* Styles */
import "./style.scss";

function HomeLayout({ images }) {
  const rows = getRows(images);

  return (
    <div id="HomeLayout">
      <h1 className="bold">Our Team</h1>
      <p className="bold">
        These are the people living the "Junior developer for life" way
      </p>
      <div>{rows}</div>
    </div>
  );
}

/**
 * Return the images in rows with 3 or 4 avatars
 * @param {Array} images
 */
function getRows(images) {
  let rows = [],
    row = [];
  let count = 0,
    aux = 3;

  for (let i = 0; i < images.length; i++) {
    count++;
    if (count <= aux) {
      row.push(
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={116}
          height="100%"
          viewBox="0 0 135 135"
        >
          <pattern
            id="amw-image"
            height="100%"
            width="100%"
            patternContentUnits="objectBoundingBox"
            viewBox="0 0 1 1"
            preserveAspectRatio="xMidYMid slice"
          >
            <image
              height={1}
              width={1}
              preserveAspectRatio="xMidYMid slice"
              xlinkHref={images[i].images.thumbnail.url}
            />
          </pattern>
          <path
            fill="url(#amw-image)"
            stroke="#dd3333"
            strokeWidth="0.7"
            d="M59.014718625761 3.8603896932107a12 12 0 0 1 16.970562748477 0l55.154328932551 55.154328932551a12 12 0 0 1 0 16.970562748477l-55.154328932551 55.154328932551a12 12 0 0 1 -16.970562748477 0l-55.154328932551 -55.154328932551a12 12 0 0 1 0 -16.970562748477 Z"
          />
        </svg>
      );
    } else {
      rows.push(
        <div key={`row-${i}`} className="row">
          {row}
        </div>
      );
      aux = aux === 3 ? 4 : 3;
      count = 0;
      row = [];
    }
  }

  return rows;
}

HomeLayout.propTypes = {
  images: PropTypes.array.isRequired
};

export default HomeLayout;
